$BackColor: #2B2B2B;
$HeaderColor: #191919;
$Font: 'Roboto', sans-serif;
$FontColor: #EFEFEF;
$Max-width:1920px;
$Max-height:900px;
$Min-width:320px;
$Min-height:556px;

body {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: $BackColor ;
}

.container {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    min-width: $Min-width;
    min-height: $Min-height;
    max-width: $Max-width;
    max-height: $Max-height; 
}

table {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: auto;
    
   
}

.col-1 {
            width: 8.33333333333%;

        }

        .col-2 {
            width: 16.6666666667%;
        }
        .col-3 {
            width:25%;
        }
        .col-4 {
            width: 33.3333333333%;

        }
        .col-5 {
            width: 41.6666666667%;
        }
        .col-6 {
            width:50%;
        }
        .col-7 {
            width: 58.3333333333%;
        }
        .col-8 {
            width:66.6666666666%;
        }
        .col-9 {
            width:75%;
        }
        .col-10 {
            width: 83.3333333333%;
        }
        .col-11 {
             width:91.6666666666%; 
        }
        .col-12 {
             width: 100%;
        }

        [class*="col-"] {
          float:left; 
        }

        [class*="col-"]:last-of-type {
        }

        .row:before
            :after
        {
          clear: both;
          content:"";
        }

nav {
    margin: 0 auto;
    padding: 0;
    width: 90%;
    text-align: right;
    
    a {
        font-family: $Font;
        font-size: 100%;
        text-decoration: none;
        color: $FontColor;
        margin-left: 2%;
    }
}

#logo-box {
    
    margin: 0 auto;
    padding: 0;
    position: relative;
    top:  25%;
    left:5%;
    
    .logo {
        margin: 0;
        padding: 0;
        width: 100%;
    }
}

#img {
    margin: 0 auto;
    padding: 0;
    position: relative;
    bottom: 0;
    padding-top: 15%;
    right: 1%;
    
    
    .image {
        margin: 0;
        padding: 0;
        width: 60%;
    }
}

#txt {
    margin: 0 auto;
    padding: 0;
    position: relative;
    bottom: 0;
    right: 5%;
    padding-top: 15%;
    
    h1 {
        font-family: $Font;
        font-size: 150%;
        color: $FontColor;
    }
    
    p {
        font-family: $Font;
        font-size: 120%;
        color: $FontColor;
    }
}

#links {
    margin: 0 auto;
    padding: 0;
    position: relative;
    bottom: 0;
    left: 35%;
    padding-top: 15%;
    text-align: center;
    
    
    .media-links {
        margin: 0 auto;
        padding: 0;
        width:2%;
        margin-left: 2%;
        
    }
}



